import React, { Suspense, lazy } from "react";
import { isTagShop } from "../../../../actions/api";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
const MediaIcon = lazy(() => import('../../Elements/MediaIcon/MediaIcon'));
const Media = ({ Post, ThemeStyle, index, inView }) => {
  const mediaIcon = Post.hotspot ? false : true

  const isVideo = (Post.type === 3 || Post.type === 5) && (ThemeStyle.autoPlay || !isTagShop) ? true : false
  const size = 100
  return <div className="tb_gt_media_wrap">
        <Suspense fallback={<></>}><MediaIcon Post={Post} IconClass="tb_gt_media_icon" show={mediaIcon} hideVideo={ThemeStyle.autoPlay} /></Suspense>
        {isVideo ? 
        <Video VideoClass="tb_gt_video" Post={Post} size={size} isCover={true} controls={false} autoPlay={ThemeStyle.autoPlay} muted={true} index={index} inView={inView}/>
        : 
        <Image ImageClass="tb_gt_image" Post={Post} size={size} hotspot={isTagShop} blurBG={isTagShop} index={index} inView={inView} /> 
        }
      </div>
}
export default Media